.react-root-container {
  /* for QDM enviroment */
  max-width: 1000px;
  font-size: 16px; /* fix font size */
  /* align center */
  margin-left: auto;
  margin-right: auto;
  display: block;
}

/* for QDM enviroment */
.react-root-container p, .react-root-container pre, .react-root-container button, .react-root-container input, .react-root-container optgroup, .react-root-container select, .react-root-container textarea, .react-root-container a, .react-root-container a:visited {
  font-family: 'SF Pro TC', 'SF Pro Text', 'PingFang TC', 'Segoe UI', SegoeUI, 'Microsoft JhengHei', 微軟正黑體, 'Helvetica Neue', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.miro-subcontainer {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 85%;
}

.digital-coin-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.a-digital-coin {
  width: 33.33%; /* 排版成三欄 */
  display: flex;
  flex-direction: column;
  align-items: center;
}

button.digital-coin-icon {
  border: 2px solid black;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  margin: 0.5rem;
}

img.digital-coin-icon {
  display: inline-block;
  padding-top: 2rem;
  padding-bottom: 0rem;
  width: 6rem;
  height: auto;
  margin: 0.5rem;
}

.miro-footer {
  width: 100%;
  height: 15rem;
}

.miro-text-center {
  text-align: center;
}

.miro-center {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.miro-right {
  display: block;
  margin-left: auto;
  margin-right: 0;
}

.miro-left {
  display: block;
  margin-left: 0;
  margin-right: auto;
}

.miro-regular-items {
  display: flex; /* 使用flexbox排版 */
  flex-direction: row; /* 元件主軸為row，所以會從左到右排序 */
  justify-content: center; /* 元件置中排列 */
  align-items: center; /* 元件縱軸置中排列 */
  padding-left: 5%;
  padding-right: 5%;
  margin-bottom: 2rem;
}

.miro-regular-items > .content-img {
  width: 20%;
  max-width: 100px;
}

.miro-regular-items > .content {
  width: 60%;
  padding: 1rem;
}

.miro-regular-items > .content > .content-title {
  font-size: large;
  text-align: left;
  font-weight: bold;
}

.miro-regular-items > .content > .content-desc {
  font-size: 0.8rem;
}

.miro-regular-items > .donate-btn {
  width: auto;
}

.miro-smaller-text {
  font-size: 0.8rem;
}

@media (min-width: 600px) and (max-width: 700px) {
  .miro-regular-items > .donate-btn {
    width: 20%;
    text-align: center;
  }
}

/* 解析度600px以下時套用另一種排版方式 */
@media (max-width: 600px) {
  .miro-regular-items {
    display: flex; /* 使用flexbox排版 */
    flex-direction: column; /* 元件主軸為row，所以會從左到右排序 */
    padding: 5%;
    margin: 3%;
    border-radius: 5%;
    background-color: rgb(234, 234, 234);
  }
  .miro-regular-items > .content-img {
    width: 60%;
    max-width: unset;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  .miro-regular-items > .content {
    width: 100%;
  }
  .miro-regular-items > .donate-btn {
    width: 100%;
    display: flex;
    justify-content: center;
  }
}

.miro-title {
  text-align: left;
  font-weight: bold;
}

.miro-show-animate {
  transition: all 1s ease-out;
}

/*
768px
1000px
*/

.miro-btn-for-donate-unit {
  text-decoration: none;
  font-weight: bold;
  color: #eceff2;
  background-color: black;
  padding: 0.75rem;
  margin: 0.2rem;
  border-radius: 0.6rem;
}

.miro-next-button {
  text-decoration: none;
  padding: 0.6rem;
  background: coral;
  border:1px solid coral;
  cursor: pointer;
  border-radius: 0.6rem;
  color: white;
  line-height: 1.2rem;
}

/* for QDM enviroment */
.miro-next-button a, .miro-next-button a:hover, .miro-next-button a:visited {
  color: white;
  text-decoration: none;
}

.miro-text-button {
  text-decoration: none;
  padding: 0.6rem;
  background: #eee;
  border: none;
  cursor: pointer;
  border-radius: 0.6rem;
  color: black;
  line-height: 1.2rem;
}

.miro-text-button > i {
  margin-right: 0.4rem;
  line-height: 1.2rem;
}

.miro-other-inner > p {
  text-align: center;
  font-size: 20px;
}

.miro-other-inner a {
  text-align: center;
  font-size: 14px;
  text-decoration: none;
}
