/* https://medium.com/@mingjunlu/building-a-simple-navbar-with-html-and-css-setting-up-the-skeleton-212fb8767cb5 */

nav.miro-menu a {
    text-decoration: none;  /* 移除超連結底線 */
    text-align: center; /* 元件內文字置中 */
    display: block; /* 填充區塊,類似p標籤 */
    /* 標準顯示 */
    font-size: 1rem;
    padding-left: 1rem;
    padding-right: 1rem;
    line-height: 2.5rem;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    border-radius: 0.5rem;
    box-shadow: 0px 0px 5px 0px #aaa;
}

/* 解析度480px以下時套用比較窄的邊緣和字體 */
@media (max-width: 480px) {
    nav.miro-menu a {
        padding-left: 0.75rem;
        padding-right: 0.75rem;
    }
}

/* 解析度767px以上時套用比較寬的邊緣和字體 */
@media (min-width: 767px) {
    nav.miro-menu a {
        font-size: 1.25rem;
        padding-left: 2rem;
        padding-right: 2rem;
        line-height: 3rem;
    }
}

nav.miro-menu > ul {
    list-style: none;   /* 移除項目符號 */
    margin: 0; /* 移除ul標籤預設 */
    padding: 0; /* 移除ul標籤預設 */
    margin-bottom: 0.5rem;
    display: flex; /* 使用flexbox排版 */
    flex-direction: row; /* 元件主軸為row，所以會從左到右排序 */
    justify-content: center; /* 元件置中排列 */
    align-items: center; /* 元件縱軸置中排列 */
    flex-wrap: wrap;
}