.miro-crytpo-form {
    width: 75vw;
    max-width: 768px;
    border: 1px solid #ddd;
    border-radius: 0.6rem;
    padding: 3vw;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
}

.miro-crytpo-form > label {
    margin-bottom: 15px;
    position: relative;
    border-bottom: 1px solid rgb(150, 53, 53);
}

.miro-crytpo-form > label > input {
    width: 100%;
    padding: 10px 0px;
    margin-top: 20px;
    border: none;
    outline: none;
}
