.miro-progress-pointer {
    margin-left: 0.5rem;
}

.miro-progress-block {
    width: 85%;
    margin: auto;
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.miro-progress-amount font {
    font-weight: bold;
    font-size: 1.1em;
}

.miro-progress-amount {
    display: flex;
    justify-content: space-between;
}

.miro-progress-bubble {
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 5px 10px;
    margin: 3px 10px;
}

.miro-progress-goal {
    align-self: center;
}

.miro-progress-wrapper {
    background: #eceff2;
    border-radius: 2px;
    overflow: hidden;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    align-items: center;
}

.miro-progress-bar {
    background-color: red;
    height: 30px;
    border-radius: 2px 0 0 2px;
    box-shadow: 0px 0px 5px 0px #aaa;
    transition: width 1.5s ease;
}